/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
	SVG Loader by Aurer:
	https://codepen.io/aurer/pen/jEGbA
*/
*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: #454545;
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

body {
  background-color: #EDEDED;
  padding: 20px;
  color: #000;
}

section {
  border-bottom: 1px solid #D6D6D6;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

section:last-of-type {
  border-bottom: 0;
}

section div {
  padding-bottom: 10px;
}

section div > label:not(.switch) {
  display: block;
  float: left;
  width: 138px;
  text-align: right;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  padding-right: 10px;
}

section div > label:not(.switch)::after {
  content: ':';
}

section div select,
section div input[type="range"] {
  display: block;
  width: calc(100% - 138px);
  font-size: 13.5px;
}

section div select {
  height: 22px;
}

section div datalist {
  display: flex;
  justify-content: space-between;
  margin-left: 138px;
}

section div datalist option {
  line-height: 20px;
  font-size: 20px;
}

section div datalist option:first-of-type {
  font-size: 10px;
}

section div .switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
}

section div .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

section div .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 22px;
  transition: .25s;
}

section div .slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  border-radius: 50%;
  transition: .25s;
}

section div input:checked + .slider {
  background-color: #2196F3;
}

section div input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

section div input:checked + .slider:before {
  transform: translateX(17px);
}

section div li label {
  font-size: 13px;
}
